.primary-display{
    height: 80%;
    width: 80%;
    background-color: #4cd137;
    border-radius: 0.5rem;
    border-top-left-radius: 5rem;
    color: #34495e;
    display: flex;
    justify-content: center;
    align-items: center;
}

.display-state{
    height: 85%;
    width: 85%;
    background-color: #4cd137;
    border-radius: 0.5rem;
    color: #34495e;
}

.pokeimg{
    height: 120px;
    width: 85%;
}

.mini-statespoke{
    height: 20px;
    width: 20px;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    color:#34495e;
    font-size: small;
    font-weight: bolder;
}

.mini-statespoke>p{
    margin : 0;
    padding: 0;
}

.directions{
    display:flex;
    flex-direction:row;
    justify-content: space-around;
}